.btn {
  min-height: 40px;
  padding: 0 1.5rem;
  color: #050504;
  font-family: "BlenderPro", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.btn[disabled] {
  opacity: 1;
}
.btn-primary {
  border: none;
  background-color: #ffd000;
}
.btn-primary:hover {
  color: currentColor;
  background-color: #dab201;
}
.btn-primary-alt {
  color: #FFF;
  background-color: #060605;
}
.btn-primary-alt:hover {
  background-color: #363635;
}
.btn-primary-alt.btn-reversed {
  color: #060605;
  background-color: #fff;
}
.btn-primary-alt.btn-reversed:hover {
  color: #FFF;
  background: #363635;
}
.btn-primary[disabled], .btn-primary[disabled]:hover, .btn-primary-alt[disabled], .btn-primary-alt[disabled]:hover, .btn-csr[disabled], .btn-csr[disabled]:hover {
  color: #AAAAA3;
  background-color: #f8f8f7;
}
.btn-primary[disabled].btn-reversed, .btn-primary[disabled]:hover.btn-reversed, .btn-primary-alt[disabled].btn-reversed, .btn-primary-alt[disabled]:hover.btn-reversed, .btn-csr[disabled].btn-reversed, .btn-csr[disabled]:hover.btn-reversed {
  color: #575754;
  background-color: rgba(47, 47, 47, 0.5);
}
.btn-secondary {
  color: #060605;
  border-color: #575754;
  background-color: transparent;
  will-change: box-shadow;
  transition: box-shadow 0.1s ease-in-out;
}
.btn-secondary:not([disabled]):hover {
  color: currentColor;
  box-shadow: inset 0 0 0 1px #363635;
  border-color: #363635;
  background-color: transparent;
}
.btn-secondary.btn-reversed {
  color: #FFF;
  border-color: #FFF;
}
.btn-secondary.btn-reversed:not([disabled]):hover {
  color: #FFF;
  border-color: #FFF;
  box-shadow: inset 0 0 0 1px #FFF;
}
.btn-secondary.btn-reversed[disabled], .btn-secondary.btn-reversed[disabled]:hover {
  color: #575754;
  background-color: transparent;
  border-color: #575754;
}
.btn-secondary[disabled], .btn-secondary[disabled]:hover {
  color: #AAAAA3;
  background-color: transparent;
  border-color: #e0e0da;
}
.btn-tertiary {
  border: none;
  background-color: transparent;
}
.btn-tertiary:hover {
  color: currentColor;
  background-color: #e6e6e6;
}
.btn-tertiary.btn-reversed {
  color: #FFF;
}
.btn-tertiary.btn-reversed:hover {
  background-color: #2f2f2f;
}
.btn-tertiary.btn-reversed[disabled], .btn-tertiary.btn-reversed[disabled]:hover {
  color: #575754;
  background-color: rgba(47, 47, 47, 0.5);
}
.btn-tertiary[disabled], .btn-tertiary[disabled]:hover {
  color: #AAAAA3;
  background-color: transparent;
}
.btn-csr {
  color: #FFF;
  border: none;
  background-color: #0B8339;
}
.btn-csr:hover {
  background-color: #0B8339;
}